.body {
    
    
    background-image: url("https://estaticos.qdq.com/swdata/home_photos/389/389154911/c22e27f83dc640f2a5bb0ed4417b44ff.jpg");
    height: 100vh;
width: 100vw;
    background-size: cover;
    background-position: center;
   

}

.containerButtons {
height: 36em;
width: 100em;
margin-top: 0.3em;
margin-left: -3em;
border-radius: 0.4em;
display: flex;
justify-content: space-around;
align-items: center;
flex-direction: column;
position: fixed;
box-shadow: inset rgba(0, 0, 0, 0.652) 0.2em 0.1em 1em 0.1em;

}




.buttonsProfile {
width: 18em;
height: 3em;
font-size: x-large;
border-radius: 1em;
background-color: rgba(13, 120, 182, 0.768);
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
box-shadow: inset rgba(0, 0, 0, 0.652) 0.2em 0.1em 1em 0.1em;
a {
    color: white;
}
}

.buttonsProfile:hover {
            box-shadow: 0em 0em 0.8em 0.6em rgba(255, 255, 255, 0.644);
            transition: 0.3s;
        }
    
